import { Component, OnChanges, Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

interface Config {
  files?: string[];
  fileName?: string;
  basePath?: string;
  extensions?: string[];
}

const LanguageFullname = {
  ts: 'typescript',
  html: 'html',
  scss: 'scss',
  css: 'css'
};

const DefaultExtensions = ['html', 'ts', 'scss'];

@Component({
  selector: 'app-example-viewer',
  templateUrl: './example-viewer.component.html',
  styleUrls: ['./example-viewer.component.scss']
})
export class ExampleViewerComponent implements OnChanges {

  @Input() config: Config = {};

  files: string[] = [];
  filesData: { [key: string]: string } = {};
  showFiles = false;

  constructor(private http: HttpClient) { }

  ngOnChanges() {
    if (this.config.files) {
      this.files = this.config.files;
    } else if (this.config.basePath && this.config.fileName) {
      this.files = (this.config.extensions || DefaultExtensions)
        .map(ext => `${this.config.basePath}/${this.config.fileName}.${ext}`)
        .map(file => file.replace(/^\//, ''));
    } else {
      this.files = [];
    }
  }

  toggleDisplay() {
    this.showFiles = !this.showFiles;

    this.files.map((file) => {
      if (!this.filesData.hasOwnProperty(file)) {
        this.filesData[file] = 'Loading ...';
        this.http.get(file, { responseType: 'text' }).pipe(
          catchError(err => of('Error in loading content ...'))
        ).subscribe((content) => this.filesData[file] = content);
      }
    });
  }

  guessLanguages(fileName = '') {
    const extension = fileName.match(/\.(\w+)$/)[1];
    const lang = LanguageFullname[extension];

    return lang ? [lang] : [];
  }

}
